export default {
	data() {
		return {
			progress: 100,
			countdown: 5
		};
	},

	methods: {
		onChange([s]) {
			this.progress = parseInt((s * 100) / 60);
		},

		onDone() {
			this.$refs["toast"].open("Game Over");
			this.$refs["cd"].start({ second: 10 });
		}
	}
};
